.nav_icon3 {
  width: 60px;
  height: 45px;
  position: absolute;
  top: 3em;
  right: 3em;
  margin: 0;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: .5s ease-in-out;
  -moz-transition: .5s ease-in-out;
  -o-transition: .5s ease-in-out;
  transition: .5s ease-in-out;
  cursor: pointer; }

.nav_icon3 span {
  display: block;
  position: absolute;
  height: 6px;
  width: 100%;
  background: #000;
  border-radius: 0;
  opacity: 1;
  left: 0;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: .25s ease-in-out;
  -moz-transition: .25s ease-in-out;
  -o-transition: .25s ease-in-out;
  transition: .25s ease-in-out; }

.nav_icon3 span:nth-child(1) {
  top: 0px; }

.nav_icon3 span:nth-child(2), .nav_icon3 span:nth-child(3) {
  top: 18px; }

.nav_icon3 span:nth-child(4) {
  top: 36px; }

.nav_icon3.open span:nth-child(1) {
  top: 18px;
  width: 0%;
  left: 50%;
  background: #fff; }

.nav_icon3.open span:nth-child(2) {
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transform: rotate(45deg);
  background: #fff; }

.nav_icon3.open span:nth-child(3) {
  -webkit-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  transform: rotate(-45deg);
  background: #fff; }

.nav_icon3.open span:nth-child(4) {
  top: 18px;
  width: 0%;
  left: 50%;
  background: #fff; }

